@font-face {
  font-family: Molot;
  src: url(/fonts/MolotRegular/MolotRegular.woff);
}

.logo {
  font-family: Molot, sans-serif;
  text-transform: uppercase;
  color: white;
  text-shadow: 1px 0px 18px black;
  font-size: 30px;
}

.logo:hover {
  text-shadow: 2px 0px 20px black;
}

.navbar {
  opacity: 1;
  box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.6);
  font-family: Roboto, sans-serif;
  font-weight: 500;
  transition: 0.33s;
}

.navbar a {
  color: #292E37 !important;
}

.navbar a:hover {
  opacity: 0.8;
}

@media (min-width: 768px) {
  .dropdown-menu {
    display: block;
    opacity: 0;
    transition: opacity 0.33s;
    background-color: #f8f8f8;
    border-top: none;
    right: -1px !important;
    padding: 0;
    border-radius: 0 0 0 3px;
    pointer-events: none;
  }
  .dropdown-menu li {
    padding: 0;
  }
  .dropdown-menu li:hover, .dropdown-menu li a:hover {
    background-color: #c4c4c4;
  }
  .dropdown-menu li a {
    padding: 15px;
  }
}

@media (min-width: 768px) {
  .dropdown:hover .dropdown-menu {
    opacity: 1;
    pointer-events: all;
  }
}

.footer {
  background-color: #292E37;
  color: white;
  width: 100%;
}

.footer__icon-row {
  display: flex;
  width: 100%;
  text-align: center;
  justify-content: space-around;
  padding: 50px 0 30px 0;
  flex-direction: row;
}

.footer__copyright-row {
  text-align: center;
  font-size: 14px;
  width: 100%;
  padding: 30px 0 50px 0;
}

.modal {
  position: fixed;
  display: block;
  overflow: hidden;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-color: rgba(255, 255, 255, 0.9);
  opacity: 1;
  transition: opacity 0.3s;
}

.modal_hidden {
  opacity: 0;
  pointer-events: none;
}

.modal-wrapper {
  display: flex;
  justify-content: center;
  align-content: center;
  width: 100%;
  height: 100%;
  cursor: zoom-out;
}

.modal__container {
  height: 90vh;
  width: 95vw;
  margin: auto;
  vertical-align: middle;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}

.modal__close-button {
  position: absolute;
  right: 0;
  top: 0;
  width: 50px;
  height: 50px;
  opacity: 0.6;
  cursor: pointer;
}

.modal__close-button:hover {
  opacity: 1;
}

.modal__close-button:before, .modal__close-button:after {
  content: "";
  position: absolute;
  background-color: black;
  left: 20px;
  bottom: 0;
  width: 2px;
  height: 100%;
  display: block;
}

.modal__close-button:before {
  transform: rotate(45deg);
}

.modal__close-button:after {
  transform: rotate(-45deg);
}

.circle-icon {
  border-radius: 100%;
  text-align: center;
  margin: auto;
}

.circle-icon:hover {
  opacity: 0.8;
}

.cover-with-face {
  width: 100%;
  text-align: center;
  padding-bottom: 40px;
}

.cover-with-face__cover {
  width: 100%;
  height: 200px;
  background-size: cover;
  background-attachment: scroll;
}

@media (min-width: 576px) {
  .cover-with-face__cover {
    height: calc(60vh - 50px);
  }
}

.cover-with-face__face {
  margin: auto;
  width: 100px;
  height: 100px;
  margin-top: -50px;
  background-size: cover;
  border-radius: 100%;
  border: 2px solid white;
}

@media (min-width: 576px) {
  .cover-with-face__face {
    width: 160px;
    height: 160px;
    margin-top: -80px;
  }
}

.text {
  padding: 40px 0 30px 0;
}

.text_key-phrase {
  font-size: 26px;
  text-align: center;
  padding: 60px 0 45px 0;
}

.feature-post {
  text-align: center;
  padding: 60px 0 75px 0;
}

.feature-post a {
  text-decoration: none;
}

.feature-post a:hover {
  opacity: 0.8;
}

.feature-post__caption {
  text-transform: uppercase;
  color: #292E37;
  font-size: 14px;
}

.feature-post__title {
  color: #FF4D4D;
  font-size: 34px;
  padding: 15px 0 5px 0;
}

.feature-post__title:hover {
  opacity: 0.8;
}

.feature-post__subtitle {
  color: #292E37;
  margin-bottom: 30px;
}

.circle-image {
  border-radius: 100%;
  margin: auto;
}

.img-zoomable {
  cursor: zoom-in;
  margin: auto;
}

.title-slide {
  height: 100vh;
  margin-top: -50px;
  display: flex;
  justify-content: center;
  text-align: center;
}

.title-slide__bg {
  background-attachment: fixed;
  background-size: cover;
  backgound-repeat: no-repeat;
  background-position: center center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.title-slide__overlay {
  background-color: black;
  opacity: 0.4;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.title-slide__container {
  margin: auto;
  z-index: 20;
}

.title-slide__content {
  color: white;
  max-width: 100vw;
  max-height: 100vh;
}

.title-slide__content h1 {
  color: white;
  font-size: 36px;
  padding-bottom: 50px;
}

@media (min-width: 768px) {
  .title-slide__content h1 {
    font-size: 72px;
  }
}

.title-slide__content p {
  font-size: 16px;
}

@media (min-width: 768px) {
  .title-slide__content p {
    font-size: 30px;
  }
}

.chapter-title {
  text-align: center;
  padding: 50px 0;
}

.chapter-title h2 {
  font-size: 30px;
  word-break: break-all;
  max-width: 100%;
}

@media (min-width: 576px) {
  .chapter-title h2 {
    font-size: 50px;
  }
}

@media (min-width: 768px) {
  .chapter-title h2 {
    font-size: 60px;
  }
}

.chapter-title p {
  font-size: 30px;
}

.image-with-text {
  padding: 50px 0 30px 0;
}

@media (max-width: 575px) {
  .list ul {
    padding: 0;
  }
}

.list li {
  list-style-type: none;
  padding-top: 10px;
}

.image-with-caption {
  text-align: center;
}

.image-with-caption img {
  padding: 20px 0 20px 0;
}

.list-alternating-images__caption {
  font-size: 14px;
}

.list-alternating-images__caption_subtext {
  font-size: 10px;
}

.post-list-element {
  margin: 0;
}

.post-list-element a p {
  color: #292E37;
}

.post-list-element__section {
  padding: 0;
}

.post-list-element__text {
  padding: 40px 20px 20px 20px;
  border-left: 1px solid #c4c4c4;
  border-top: 1px solid #c4c4c4;
}

@media (min-width: 768px) {
  .post-list-element__text {
    height: 350px;
  }
}

.post-list-element__category {
  color: #FF4D4D;
}

.post-list-element__image-wrapper {
  position: relative;
  height: 350px;
}

.post-list-element__image {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.post-list-element:last-child .post-list-element__text {
  border-bottom: 1px solid #c4c4c4;
}

